<template>
    <div>
      <div>
        <span style="margin-left: 27px;font-weight: 600;">Product Menu</span>
        <el-menu
          :default-active="activeIndex"
          class="el-menu-vertical-demo"
          style="width: 80%;"
          @select="handleSelect"
        >
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-s-promotion"></i>
              <span>USB-C CABLE</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="USB-C CABLE_A-C">A-C</el-menu-item>
              <el-menu-item index="USB-C CABLE_C-C with E-marker">C-C with E-marker</el-menu-item>
              <el-menu-item index="USB-C CABLE_C-usb3.0 AF">C-usb3.0 AF</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-menu-item v-for="item in menuItems" :key="item" :index="item">
            <i class="el-icon-s-promotion"></i>
            <span slot="title">{{item}}</span>
          </el-menu-item>
        </el-menu>
      </div>
      <div class="contact_card">
        <el-card class="box-card">
          <div class="clearfix">
            <span style="font-style: oblique;font-family: -webkit-body;font-weight: 800;">Contact Us</span>
          </div>
          <ul class="user-info">
            <li>Contact :<div class="user-right">{{ user.username }}</div></li>
            <li>Phone :<div class="user-right">{{ user.phone }}</div></li>
            <li>Mail :<div class="user-right">{{ user.mail }}</div></li>
            <li>Address :<div class="user-right" style="float: none;margin-top: 10px;"> {{ user.address }}</div></li>
          </ul>
        </el-card>
      </div>
    </div>
</template>

<script>
export default {
  name: 'LeftMenu',
  props: {
    activeIndex: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      user: {
        username: 'May',
        phone: '18138219389',
        mail: 'Support@corecabe.com',
        address: 'No.6 Yinkeng Road, Shangyuan Village, Qingxi Town,Dongguan, Guangdong,China'
      },
      menuItems: [
        'HDMI CABLE',
        'DISPLAYPORT CABLE'
      ]
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      // 子组件事件传递到 -> ProductList.vue
      this.$emit('change', key)
    }
  }
}
</script>

<style scoped rel="stylesheet/scss" lang="scss">
.user-info {
  padding-left: 0px;
  list-style: none;
  li{
    border-bottom: 1px solid #F0F3F4;
    border-top: 0px solid #F0F3F4;
    padding: 11px 0px;
    font-size: 13px;
  }
  .user-right {
    float: right;
    a{
      color: #317EF3;
    }
  }
}
</style>



